import React from 'react'
import { fetchMInfo } from '../actions/index.js'
import {connect} from 'react-redux'
import styles from './componentStyles/component.module.css'

class MarsWeather extends React.Component{

    componentDidMount()
    {
        this.props.fetchMInfo()
    }

    render()
    {
        if(this.props.marsInfo){
            const mar = this.props.marsInfo
            // console.log(mar)
            const sols = this.props.marsInfo.sol_keys.slice(0,4)
            // console.log(sols)
            const solar_day = sols.map(sol => {
                const sol_day = mar[sol]
                // console.log(mar[sol].AT)
                return(
                <div className = {`four wide column , ${styles.card_container}`}  key={sol}>
                    <div className ="content">
                    <div className = {`header , ${styles.season}`}>
                        {sol_day.Season}
                        </div>
                        <div className = {styles.title_solar}>
                            {`Solar Day : ${sol}`}
                            </div>
                            
                            <div className = {`ui grid , ${styles.temp_gird}`}>
                                <div className = {`column , ${styles.temp_card}`}>
                                    <div className = {styles.title}>
                                        AT
                                        </div>
                                                <h3>Maximum : {sol_day.AT.mx}</h3>
                                                <h3>Minimum : {sol_day.AT.mn}</h3>
                                                <h3>Average : {sol_day.AT.av}</h3>
                                    </div>
                                <div className = {`column , ${styles.temp_card}`}>
                                    <div className = {styles.title}>
                                        HWS
                                        </div>
                                                <h3>Maximum : {sol_day.HWS.mx}</h3>
                                                <h3>Minimum : {sol_day.HWS.mn}</h3>
                                                <h3>Average : {sol_day.HWS.av}</h3>
                                    </div>
                                    
                                </div>         
                    </div>
                    
                    </div>
            )})
        return(
            <div className = {styles.marsweather}>
                <div>
                <p>Mars Weather Info From Curiosity</p>
                </div>
                <h3>Here, AT stands for Atmospheirc Pressure and HWS stands for Horizontal Wind Speed for more info visit <a href="https://api.nasa.gov/assets/insight/InSight%20Weather%20API%20Documentation.pdf" target="blank">Link</a></h3>
                <div className={`ui grid , ${styles.container}`}>
                    {solar_day}
                    </div>
                </div>)}
                return(<div>Loading...</div>)
    }
}

const mapStateToProps = (state) => {
    return { marsInfo : state.marRoverinfo}
}

export default connect(mapStateToProps , { fetchMInfo })(MarsWeather)