import { fetchMarsInfo , fetchPIC , fetchMarsRoverPic} from '../APIS/index'

export const fetchMInfo = () => async dispatch => {
    const response = await fetchMarsInfo.get()
    dispatch({ type : "FETCH_MARS_INFO" , payload : response.data})
}

export const fetchP = () => async dispatch => {
    const response = await fetchPIC.get()
    dispatch({ type : 'FETCH_PIC' , payload : response.data})
}

export const fetchMRPic = () => async dispatch => {
    const response = await fetchMarsRoverPic.get()
    dispatch({ type: 'FETCH_MARS_PHOTO' , payload: response.data})
}