import React from 'react'
import image from './Images/mars-rover2.jpg'
import styles from './Style.module.css'
import Library from './components/Library'

class App extends React.Component
{
    render()
    {
        return(
            <div>
            <img className = {styles.mars_rover_image} src={image} alt="mars-rover"/>
            <h1 className = {styles.header}>Mars Rover</h1>
            <p className = {styles.para}>
                An initiative by a Nerd for Nerds out there, to live the Astronomy Culture 
                </p>
                <Library />
            </div>
        )
    }
}

export default App