import React from 'react'
import { fetchP } from '../actions/index'
import {connect } from 'react-redux'
import styles from './componentStyles/PicOfTheDay.module.css'

class PicOfTheDay extends React.Component{

    componentDidMount()
    {
        this.props.fetchP()
    }

    render()
    {
        if(this.props.PIC){
        return(
            <div>
            <div className = {styles.pic_container}>
              <center><p>Space Pic of the Day</p></center>
                </div>
                <div className={styles.ui_container}>
                    <div className="image">
                      <center>  <img className={styles.image_container} src = {this.props.PIC.hdurl} alt={this.props.PIC.title} /></center>
                    </div>
                    <div className={`content , ${styles.card_content}`}>
        <p>{this.props.PIC.title}</p>
        <div className="meta">
            <span className={`data , ${styles.card_date}`}>{this.props.PIC.date}</span>
            </div>
            <div className={`description , ${styles.image_desc}`} >
                {this.props.PIC.explanation}
                    </div>
                    </div>
                    </div>
                </div>
        )}
        return(<div>Loading...</div>)
    }
}

const mapStateToProps = (state) => {
    return { PIC : state.PIC}
}

export default connect(mapStateToProps , {fetchP})(PicOfTheDay)  