import React from 'react';
import styles from './componentStyles/component.module.css'
import ScrollIntoView from 'react-scroll-into-view';
import MarsRoverPhoto from './MarsRoverPhoto'
import PicOfTheDay from './PicOfTheDay'
import MarsWeather from './MarsWeather'
import About from './About'

class Library extends React.Component{
    render(){
        return(
            <div className = {styles.library}>
                <div className ={styles.lib_title}>
                OK, Nerds these are your options 
                </div>
                <div className = {styles.list}>
                    <ul>
                        <li>
                            <ScrollIntoView selector="#marsweather">
                            Mars Weather Info From Curiosity
                            </ScrollIntoView>
                            </li>
                        <li>
                            <ScrollIntoView selector="#pic">
                            Space Pic of the Day
                            </ScrollIntoView>
                            </li>
                        <li>
                            <ScrollIntoView selector="#pic_mars">
                            Pics from Mars Rover
                            </ScrollIntoView>
                            </li>    
                        <li>
                            <ScrollIntoView selector="#about">
                            About
                            </ScrollIntoView>
                            </li>
                        </ul>
                    </div>
                    <div id="marsweather">
                        <MarsWeather />
                        </div>
                    <div id="pic">
                        <PicOfTheDay />
                        </div>
                    <div id="pic_mars">
                        <MarsRoverPhoto />
                        </div>
                    <div id="about">
                        <About />
                        </div>        
                </div>
        )
    }
}

export default Library