const ini_state = {
    marRoverinfo : null,
    PIC : null,
    marsPhotos : null
}

export const marsRoverReducer = (state = ini_state , action) => {
    switch(action.type)
    {
        case "FETCH_MARS_INFO" : return {...state , marRoverinfo : action.payload}

        case "FETCH_PIC" : return {...state , PIC : action.payload}

        case "FETCH_MARS_PHOTO" : return{...state , marsPhotos : action.payload}

        default : return state
    }
}

