import React from 'react'
import { fetchMRPic } from '../actions/index'
import {connect} from 'react-redux'
import styles from './componentStyles/marsroverphoto.module.css'

class MarsRoverPhoto extends React.Component{

    componentDidMount()
    {
        this.props.fetchMRPic()
    }


    render()
    {
        if(this.props.Photos){
            const photos = this.props.Photos.photos.slice(0,8)
            const photos_div = photos.map( photo => {
                return(<div className="four wide column" key={photo.id}>
                    <div className="image">
                        <img className={styles.image_container} src={photo.img_src} alt={photo.id} />
                        </div>
                        <div className="content">
                            <div className={styles.image_header}>
                    {photo.rover.name}</div>
                    <div className={styles.camera_content}>
                        Camera Details :
                        <div>
                       <p className={styles.camera_name}> {`Name : ${photo.camera.name}`}</p>
                       <p className = {styles.date}> {`Date : ${photo.earth_date}`}</p>
                        </div>
                        </div>
                    </div>
                    </div>
            )})
        return(
            <div>
            <div className = {styles.mars_rover_photo}>
              <center><p>Pics from Mars Rover</p></center>
                </div>
                <div className = {`ui grid , ${styles.container}`}>
                    {photos_div}
                    </div>
               </div> 
                
                
                )}
            return( <div>Loading...</div>)
    }
}

const mapStateToProps = (state) => {
    return { Photos : state.marsPhotos}
}

export default connect( mapStateToProps , { fetchMRPic })(MarsRoverPhoto)  